import { Component, OnInit, Input } from '@angular/core';
import { ApiService } from 'src/app/services/api/api.service';
import { HelperService } from 'src/app/services/helper/helper.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FilterPipe } from 'ngx-filter-pipe';
import { bind } from '@angular/core/src/render3';
import { NgxSelectModule, INgxSelectOptions } from 'ngx-select-ex';

@Component({
  selector: 'app-areas',
  templateUrl: './areas.component.html',
  styleUrls: ['./areas.component.css']
})
export class AreasComponent implements OnInit {
  constructor(private api:ApiService, private helper:HelperService,
    private formBuilder: FormBuilder,private filter: FilterPipe) {
    }
  areas:any;
  role;
  areaForm: FormGroup;
  submitted = false;
  page:number = 1;
  pageSize:number =50;
  searchTerm:any = {title:''};
  order:string ='created_at';
  reverse=true;
  viewType = 'all'; // 'details' | 'all'
  selectedReport;
  graph
  viewDetails(data){
    this.viewType = 'details';
  // fetch all data
  this.api.getAllReports().subscribe(resp=>{
    resp.filter(x => x.area ==  data._id)
    this.selectedReport = resp
    let y=  this.selectedReport.map(r=> r.totalPatients)
    let x=  this.selectedReport.map(r=> r.submittedDate)
    this.graph={
      data:[
      { 
        y: y,
        x: x,
        //  [ this.selectedReport.totalPatients, this.selectedReport.waitingRoom, this.selectedReport.triage, this.selectedReport.utc, this.selectedReport.majors, this.selectedReport.paediatrics,this.selectedReport.resus, this.selectedReport.breached]
        type:'scatter',
        //    marker:{color:['#F44336','#00BCD4','#4CAF50','#3F51B5','#FF5722','#CDDC39','#5D4037','#EC407A']}
        }
      ]
        }
      });
  }

  selectedArea={
    code:'',
    title:'',
    department:'',
    phone:'',
    // email:'',
    status:true,
    created_at:''
  }

  sortBy(value){
    this.order = value;
  }

  reverseSort(value){
    this.order = value;
    this.reverse = !this.reverse;
  }

  get departmentKeys(){
    if(this.areas.length>0){
      let x= Object.keys(this.areas[0])
      x.splice( x.indexOf('code'), 1 );
      x.splice( x.indexOf('index'), 1 );
      return x;
    }else{
      return [];
    }
  }

  ngOnInit() {
    this.api.getAllDepartments().subscribe(response=>{
      console.log('dp', response);
      this.departments = response;
    })
    if(localStorage.getItem('role') == 'admin'){
      this.getAreas();
    }else{
     if(localStorage.getItem('role') !== 'admin'){
        this.getAreas();
      }
    }
  this.role = localStorage.getItem('role');
  this.areaForm = this.formBuilder.group({
  //  _id:[this.selectedArea._id],
    code:[this.selectedArea.code],
    title: [this.selectedArea.title, Validators.required],
    department: [this.selectedArea.department],
    // email: [this.selectedArea.email, ],
    phone: [this.selectedArea.phone ],
    created_at: [this.selectedArea.created_at ],
    status: [this.selectedArea.status, ],
    // password: ['', [Validators.required, Validators.minLength(6)]]
    });
    this.getDepartments();
  }

  onTextChange(e){
    let val = e.target.value;
    //make the array full
    this.pageSize = this.areas.length;
    console.log('event', e.target.value);
    this.searchTerm.title = e.target.value;
 //   this.areas = this.filter.transform(this.areas, { title: this.searchTerm.title });
  }

  getAreas(){
    this.helper.loading();
   this.api.getAllAreas()
    .subscribe(res=>{
      let data = res;
      let index =0;
     data = data.map((key,val)=>{
       index++;
       return {...key, index}
     });
     this.areas = data;
     console.log('areas', this.areas);
     setTimeout(()=>{
       this.helper.dismissLoading();
     },500)
    },err=>this.handleError(err));

  }

  handleError(err){
    console.log('Error: ', err.message);
  }

  add(c){
    this.helper.open(c)
  }
   // convenience getter for easy access to form fields
   get f() { return this.areaForm.controls; }

  onSubmit() {
    this.submitted = true;
    if (this.areaForm.invalid) {
      console.log('invalid department form!')
        return;
    }
    let data = this.areaForm.value;
    delete data._id; delete data.code;
    this.api.createArea(data)
    .subscribe(res=>{
      this.helper.toast('success', 'Department Created', 'Your department is active');
      this.helper.closeModal();
      this.getAreas();
    });
  }

  onUpdate() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.areaForm.invalid) {
      console.log('invalid department form!')
        return;
    }
    let data = Object.assign({}, this.areaForm.value)
    let code = data.code;
    // delete data.code; 
    delete data.code;
    this.api.updateArea(code,data.value)
    .subscribe(res=>{
      console.log('res',res);
      this.helper.toast('success', 'Department Updated', 'Your department is active');
      this.helper.closeModal();
      this.getAreas();
    });
  }

  onDelete() {
    this.submitted = true;
    this.api.deleteArea(this.selectedArea.code)
    .subscribe(res=>{
      console.log('res',res);
      if(res.success == false){
        this.helper.toast('danger', 'Operation Failed', 'Unable to delete Area.');
      }else{
        this.helper.toast('success', 'Area Deleted', 'Area successfully deleted.');

      }
      this.helper.closeModal();
      this.getAreas();
    });
  }

  edit(c, data?){
    console.log('selected area', data);
    if(!data.phone){
      data.phone = ''
    }
    delete data.index;
    this.helper.open(c)
    this.selectedArea = data;
    
    this.areaForm.setValue(this.selectedArea);

  }


  delete(c, data){
    console.log('selected department', data);
    this.selectedArea.code = data.code;
    this.helper.open(c);

    // this.api.deleteDepartment();

  }


departments;

  getDepartments(){
    this.api.getAllDepartments(res=>{
    this.departments = res;
    console.log('departments', this.departments);
    })
  }


}
