import { Component, OnInit } from '@angular/core';
import { ApiService } from 'src/app/services/api/api.service';
import { HelperService } from 'src/app/services/helper/helper.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-managers',
  templateUrl: './managers.component.html',
  styleUrls: ['./managers.component.css']
})
export class ManagersComponent implements OnInit {

  constructor(private api:ApiService, 
    private formBuilder: FormBuilder,private helper:HelperService) { }

 
  managers:any;
  roles:any;
  departments:any;
  searchTerm:any = {username:'', role:'', approved:true};
  userForm: FormGroup;
  submitted = false;


  selectedUser={
    _id:'',
    role:'',
    username:'',
    password:'',
    department:'',
    approved:false,
    phone:'',
    firstName:'',
    lastName:'',
    email:''
  }

  resetUser(){
    this.selectedUser={
      _id:'',
      role:'',
      username:'',
      password:'',
      department:'',
      approved:false,
      phone:'',
      firstName:'',
      lastName:'',
      email:''
    }
  }


  ngOnInit() {
    this.helper.loading();
   this.getAllUsers();
   this.getRoles();


   this.api.getAllDepartments().subscribe(response=>{
    this.departments = response;
  })
  
   this.userForm = this.formBuilder.group({
    //  _id:[this.selectedUser._id],
    firstName:[this.selectedUser.firstName],
    lastName:[this.selectedUser.lastName],
    username:[this.selectedUser.username, Validators.required],
    password: [this.selectedUser.password, Validators.required],
      role: [this.selectedUser.role, Validators.required],
      department: [this.selectedUser.department, ],
      approved: [this.selectedUser.approved, ],
      // password: ['', [Validators.required, Validators.minLength(6)]]
  });
  }


  getAllUsers(){
    this.api.getAllUsers().subscribe(resp=>{
      console.log(resp);
      this.managers = resp.filter(x=> x.role !== 'admin');
      this.helper.dismissLoading();
    })
  }

  changeRole(role){
    if(role == 'all'){
      role = '';
    }
    this.searchTerm.role = role;

  }

  add(c){
    this.helper.open(c)
  }

  open(content,data){
    this.selectedUser = data;
    this.helper.open(content)
  }

    // Approve user
    approveUser(id){
      this.api.updateUser(id, {approved:true}).subscribe(resp=>{
        this.getAllUsers();
        this.helper.closeModal();
        this.helper.toast('success', 'User Updated', 'User Approved');
      })
    }

    updateUser(id){
      console.log('user-data', this.selectedUser);
      let data = this.selectedUser;
      this.api.updateUser(id, data).subscribe(response=>{
        console.log('updated ');
        this.helper.closeModal();
        this.helper.toast('success', 'User updated', 'User Profile updated');
        this.resetUser();
        
      })
    }
  

    addUser(c){
      this.helper.open(c)
    }
    

    getRoles(){
      this.api.getRoles().subscribe(res=>{
        console.log('roles', res);
        this.roles = res;
      })
    }
 
    onSubmit() {
      this.submitted = true;
      console.log('mai chala');
      // stop here if form is invalid
      if (this.userForm.invalid) {
        console.log('invalid user form!')
          return;
      }
      console.log(this.userForm.value);
      let data = this.userForm.value;
      data.approved = Boolean(data.approved);
      if(data._id){
        delete data._id; delete data.code;
      }
      console.log(data);
      this.api.createUser(data)
      .subscribe(res=>{
        console.log('res',res);
        this.helper.toast('success', 'User Created', 'Your user is active');
        this.helper.closeModal();
        this.getAllUsers();
        this.resetUser();
      });
    }
  


}
