import { Component, OnInit } from '@angular/core';
import { ApiService } from 'src/app/services/api/api.service';
import { HelperService } from 'src/app/services/helper/helper.service';

@Component({
  selector: 'app-notifications',
  templateUrl: './notifications.component.html',
  styleUrls: ['./notifications.component.css']
})
export class NotificationsComponent implements OnInit {

  constructor(private api:ApiService, private helper:HelperService) { }

  ngOnInit() {
  }

  notification={
    title:'',
    message:''
  }

  open(c){
    this.helper.open(c);
  }


  generate(){
    //time to generate a push notification 
    let data = this.notification;
  this.api.generateNotification(data).subscribe(response=>{
    console.log('resp', response);
    this.helper.toast('success', 'Notification Generated','Notification');
    this.helper.closeModal();

  },err=>{
    this.helper.toast('danger', 'Error Generating Notification', ' Notification Error');
    this.helper.closeModal();

  })
  }

}
