import { Component, OnInit } from '@angular/core';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import { ApiService } from 'src/app/services/api/api.service';
import { HelperService } from 'src/app/services/helper/helper.service';
import * as moment from 'moment';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  closeResult: string;

  constructor(private modalService: NgbModal,private helper:HelperService,
     private api:ApiService) {}

  open(content) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

  managers;
  reports;
  report;
  performance;
  performances;
  graph;
  graphx;
  y
  yx;
  ngOnInit() {

    this.getAllStats();
    this.api.getManagers().subscribe(resp=>{
      console.log(resp);
      this.managers = resp;
    })
    this.api.getAllReports().subscribe(resp=>{
      console.log(resp);
      this.reports = resp;
      this.report = this.reports[0];
      let data = this.reports;
      this.y = data.map((key)=> {
        return [key.totalPatients, key.waitingRoom, key.triage, key.utc, key.majors, key.paediatrics,key.resus, key.breached]
      });
      this.graph={
        data:[
          { x: ['totalPatients','waitingRoom','Triage', 'UTC','Majors','Paediatrics','resus','Breached'],
            y:this.y[0],
            type:'bar',
            marker:{color:['#F44336','#00BCD4','#4CAF50','#3F51B5','#FF5722','#CDDC39','#5D4037','#EC407A']}
            }
        ],
        layout:{
          showlegend: false,
          title: 'Latest Report',
          xaxis: {
            title:'Report Data',
            tickfont: {
            size: 14,
            color: 'rgb(107, 107, 107)'
          },
         
        },
      
        yaxis: {
          title: 'Total Patients',
          titlefont: {
            size: 16,
            color: 'rgb(107, 107, 107)'
          },
        }
      }
      }
      
    })
    this.api.getPerformanceData().subscribe(resp=>{
      this.performances = resp;
      console.log('Performance', this.performances);
      this.performance =resp[0];
    //  let data = this.performances;
  

    let maj = this.performances.map(d=>{
         return d.majors.performance
     })
     let time =this.performances.map(d=>{
      return d.created_at
  })


      var majors = {
        x: time,
        y: maj,
     //   y: new Date(),
        type: 'scatter',
        name:"Major"
        
      };
      
      // var trace2 = {
      //   x: [1, 2, 3, 4],
      //   y: new Date(),
      //   type: 'scatter',
      //   name:'Performance'
      // };
      // let majors = this.performances.map(res=>{
      //   return res.majors.performance
      // })
      
      var data = [majors];
     this.graphx ={
       data:data,
       xaxis: {
        title: 'x-axis title'
      },
     }

    })

  }



  data:any;
  getAllStats(){
    this.helper.loading();
    this.api.getAllStats().subscribe(resp=>{
      this.data = resp;
      console.log('stats', this.data);
      this.helper.dismissLoading();
    }, err=> {
      //no response is their.
      this.helper.dismissLoading();
    //  window.location.reload();

    });
  }


  public getmoment(date){
   return moment(date).fromNow();

  }
}
